import { Component, OnInit } from '@angular/core';
import { ComandaServicioService } from '../../servicios/comanda-servicio.service';
import { Mesas } from '../../clases/mesas';

@Component({
  selector: 'app-alta-pedidos',
  templateUrl: './alta-pedidos.component.html',
  styleUrls: ['./alta-pedidos.component.css']
})
export class AltaPedidosComponent implements OnInit {

  public listado : Array<Mesas>;

  constructor(public miHttp : ComandaServicioService ) { 
    
    this.listado = new Array<Mesas>();
    
    this.lista();
    console.log(this.listado);

  }
  ngOnInit() {
  }


  lista(){
   
    return this.miHttp.listarMesas().
     then( datos => {this.listado = datos})
     .catch( error => {console.log(error);})
   
   }

   seleccionarMesa(evento){
     console.log(evento);
   }
   

}
