import { Component, OnInit } from '@angular/core';
import { Pedidos } from 'src/app/clases/pedidos';
import { Usuario } from 'src/app/clases/usuario';
import { ComandaServicioService } from '../../servicios/comanda-servicio.service';
import { LoginServicioService } from '../../servicios/login-servicio.service';


@Component({
  selector: 'app-tomar-pedidos',
  templateUrl: './tomar-pedidos.component.html',
  styleUrls: ['./tomar-pedidos.component.css']
})
export class TomarPedidosComponent implements OnInit {
  
  sector: string ;
  pedidos : Array <Pedidos> ;
  mostrarTabla: boolean;


  constructor(public miHttp : ComandaServicioService, public login : LoginServicioService) {

    this.pedidos = new Array<Pedidos>();
    this.sector = this.login.getSector();
    this.Traer();
   }

  ngOnInit() {
  }

  public Traer(){
    let body = new FormData();
     body.append('tipo', this.sector);     
    console.log(this.sector);
    this.miHttp.TraerPedidos(body)
    .subscribe(
      data => {       
        //console.log(data.text());     
        if(data.status == 200)
        {    
          let datos;      
           datos = data.json();
           this.pedidos = datos['datos'];
            //alert("Usted acaba de ingresar correctamente el empleado!");
            //location.reload();
            console.log(this.pedidos);

            if(this.pedidos.length ==0)
            {
                this.mostrarTabla = true;
            }else{

              this.mostrarTabla = false;
            }
          }
          else
            {
              alert("Error. No se pudo ingresar el empleado!");
            }
      },
      error => {
        console.error("RECIBI EL ERROR");
      }  
    );

  }

  public Tomar(id){

  }

}
