import { Component, OnInit } from '@angular/core';
import { Usuario } from 'src/app/clases/usuario';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { ComandaServicioService } from '../../servicios/comanda-servicio.service';



@Component({
  selector: 'app-formulario-alta',
  templateUrl: './formulario-alta.component.html',
  styleUrls: ['./formulario-alta.component.css']
})
export class FormularioAltaComponent implements OnInit {
  
  usuario : Usuario;
//  tipo: string;
  tipos: Array <string> ;
 
  public registroForm : FormGroup;
  myForm: FormGroup;


  constructor( public miHttp : ComandaServicioService,public fb: FormBuilder ) { 
    this.usuario = new Usuario();
   // this.usuario.tipo= this.tipo;
   this.tipos = new Array ( "Seleccionar Tipo","cocineros", "bartender", "cerveceros", "mozos", "socios");
   
   this.myForm = this.fb.group({
    nombre: ['', [Validators.required]],
    clave: ['', [Validators.required]]
     
  });

  }

  ngOnInit() {
  }
  
  InsertarUsuario() : void{
    
  
    if(this.usuario.tipo == null || this.usuario.tipo == "Seleccionar Tipo" )
    {
      alert("Seleeccione un tipo por favor");

    }else{
      console.log(this.usuario);
      this.usuario.estado = "activo";
      let body = new FormData();
     body.append('nombre', this.usuario.nombre);
      body.append('tipo',this.usuario.tipo);
      body.append('clave',this.usuario.clave);
     body.append('estado',this.usuario.estado);
      this.miHttp.InsertarUnEmpleado("InsertarEmpleado",body)
      .subscribe(
        data => {
          console.log("RECIBO LA DATA:"+ data);
          console.log(data.text());
          let respuesta : string = data.text();
          respuesta = JSON.parse(respuesta);
          console.log(respuesta);
          if(respuesta["status"] == 200)
            {
              alert("Usted acaba de ingresar correctamente el empleado!");
              location.reload();
            }
            else
              {
                alert("Error. No se pudo ingresar el empleado!");
              }
        },
        error => {
          console.error("RECIBI EL ERROR");
        }  
      );
    }
  }


  


  seleccionarTipo(tipo : string){
    this.usuario.tipo = tipo;
  }
}
