import { Component, OnInit } from '@angular/core';
import { Usuario } from 'src/app/clases/usuario';
import { ComandaServicioService } from '../../servicios/comanda-servicio.service';
import { DialogoComponent } from '../dialogo/dialogo.component';
import { resolveTiming } from '@angular/animations/browser/src/util';


@Component({
  selector: 'app-baja-empleados',
  templateUrl: './baja-empleados.component.html',
  styleUrls: ['./baja-empleados.component.css']
})
export class BajaEmpleadosComponent implements OnInit {

  public usuarios : Array<Usuario>;

  constructor( public miHttp : ComandaServicioService ) {

    this.usuarios = new Array<Usuario>();
    
    this.lista();
   }

  ngOnInit() {
  }


  async borrar(id){
  console.log(id);

  try {
      const data = await this.miHttp.BorrarUnEmpleado(id);
      if (data.status == 200) {
        this.lista();
      }
      else {
        //this.popup.show("error", data.mensaje);+
        console.info("error", data.error);
        // this.alert.mostrar("ERROR",data.error);
        window.alert(String(data.error));
      }
    }
    catch (error) {
      console.log(error);
    }
    
  }

  lista(){
   
    return this.miHttp.listar()
    .then( datos => {this.usuarios = datos})
     .catch( error => {console.log(error);})
   
   }


  




}
