import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { ComandaServicioService } from '../../servicios/comanda-servicio.service';
import { Usuario } from 'src/app/clases/usuario';

@Component({
  selector: 'app-modificar-empleados',
  templateUrl: './modificar-empleados.component.html',
  styleUrls: ['./modificar-empleados.component.css']
})
export class ModificarEmpleadosComponent implements OnInit {
  
  myForm: FormGroup;
  usuario : Usuario;
  ocultar : boolean;
  nombre : string;
  clave: string;
  tipo:string;
  tipos: string [] = [ "cocineros", "bartender", "cerveceros", "mozos", "socios"];
  estado:string;
  estados: string [] = ["activo", "baja"];
  



  constructor(public fb: FormBuilder, public miHttp : ComandaServicioService) {

    this.myForm = this.fb.group({
      id: ['', [Validators.required]]  
       
    });
    this.usuario = new Usuario();
    this.ocultar= true;

   }

  ngOnInit() {
  }


  async BuscarUsuario(id){
    console.log(id);
   
  
    try {
        const data = await this.miHttp.TraerUnEmpleado({id})
        if (data.status == 200) {
          this.usuario= data.datos;
          console.log(this.usuario);
          this.cargar(this.usuario);

        }
        else {
          this.ocultar= true;
          //this.popup.show("error", data.mensaje);+
          console.info("error", data.error);
          // this.alert.mostrar("ERROR",data.error);
          window.alert(String(data.error));
        }
      }
      catch (error) {
        console.log(error);
        this.ocultar= true;

      }
      
    }

    cargar(usuario){
      
      this.nombre= usuario.nombre;
      this.clave= usuario.pass;
      this.tipo= usuario.tipo;
      this.estado= usuario.estado;
      this.ocultar= false;
      
    }

    seleccionarTipo(tipo : string){
      this.usuario.tipo = tipo;
    }

    seleccionarEstado(estado : string){
      this.usuario.estado = estado;
    }

    guardar(){

      console.log(this.nombre,this.clave,this.tipo);
      
      
      let body = new FormData();
      body.append('id', this.usuario.id.toString());
      body.append('nombre', this.nombre);
      body.append('tipo',this.tipo);
      body.append('clave',this.clave);
      body.append('estado',this.estado);

      this.miHttp.ModificarUnEmpleado("ModificarEmpleado",body)
      .subscribe(
        data => {
          console.log("RECIBO LA DATA:"+ data);
          console.log(data.text());
          let respuesta : string = data.text();
          respuesta = JSON.parse(respuesta);
          console.log(respuesta);
          if(respuesta["status"] == 200)
            {
              alert("Usted acaba de ingresar correctamente el empleado!");
              location.reload();
            }
            else
              {
                alert("Error. No se pudo ingresar el empleado!");
              }
        },
        error => {
          console.error("RECIBI EL ERROR");
        }  
      );
      


    }

}
