import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Http, Response } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';
import { HttpHeaders, HttpClient } from '@angular/common/http';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
}


@Injectable({
  providedIn: 'root'
})
export class HttpServicioService {

  private url: string = "http://kq000525.ferozo.com/comanda/";
 /*private url: string = "http://localhost/comanda/";*/

  constructor(public http: HttpClient) { }

  public get( metodo: string): any {
    //console.log("GEt")
    return this.http.get(this.url+metodo, httpOptions)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  public getId( metodo: string,id: number) {
    return this.http.get( this.url+metodo+"/"+id, httpOptions)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  public post( metodo: string, data: Object) {
    return this.http.post(this.url + metodo, data, httpOptions)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  public delete( metodo: string,id: number) {
    return this.http.delete( this.url +metodo+"/"+id, httpOptions)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }

  public put(metodo: string, objeto: any) {
    return this.http.put(this.url +metodo, objeto, httpOptions)
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }


  
  private extractData(res: Response) {
    return res || {};
  }

  private handleError(error: Response | any) {
    return error;
  }

  public updateTokenHeaders(token: string) {
    if (token == '') {
      httpOptions.headers = httpOptions.headers.delete('Authorization');
    } else {
      httpOptions.headers = httpOptions.headers.append('Authorization', token);
    }
    console.log(httpOptions);
  }
}





