import { Component, OnInit, Input } from '@angular/core';
import { LoginServicioService } from '../../servicios/login-servicio.service';
import{ AppComponent } from '../../app.component';
import { reduce } from 'rxjs/operators';




@Component({
  selector: 'app-cabecera',
  templateUrl: './cabecera.component.html',
  styleUrls: ['./cabecera.component.css']
})
export class CabeceraComponent implements OnInit {


  mostrar: boolean;
  tipo:string ;
 
  usuario : string;
  permisoP : boolean;
  permisoS: boolean;
  permisoM: boolean;
  colores: string;

 
  constructor( private login : LoginServicioService) { 
   
    this.mostrar = this.login.isLogued();
    this.modificarColor();
  // console.log(this.mostrar);
   }

  ngOnInit() {
  }

  salir(){
    if(this.login.logOut()){
      window.alert("Ocurri un error");    
    }
    else{
      window.alert("Ustes sali correctamente!!");
    }
  }


  modificarColor(){
    if(this.mostrar == null || this.mostrar == false  ){
      this.colores =  '#440469';
      this.permisoP= true;
      this.permisoS = true;
      this.permisoM = true;
      
    }else{
      this.usuario = this.login.getSector();
      this.getColor();
    }
  }

  getColor() { 
    switch (this.usuario) {
      
      case 'cocineros':
      this.permisoP = false;
      this.permisoS = true;
      this.permisoM = true;
        this.colores= '#330000';
        break;
      case 'bartender':
      this.permisoP = false;
      this.permisoS = true;
      this.permisoM = true;
       this.colores=  '#00BFFF';
       break;
      case 'cerveceros':
      this.permisoP = false;
      this.permisoS = true;
      this.permisoM = true;
        this.colores=  '#196F3D';
        break;
      case 'mozos':
      this.permisoM = false;
      this.permisoS = true;
      this.permisoP = true;
      
        this.colores = '#FE9A2E';
        break;
      case 'socios':
      this.permisoS = false;
      this.permisoP = true;
      this.permisoM = true;
      this.colores = '#943126';
        break;
      
    }
  }


 
  }




