import { Injectable } from '@angular/core';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Router } from '@angular/router';
import { HttpServicioService } from './http-servicio.service';
import { Subject } from 'rxjs';
import { DialogoComponent } from '../componentes/dialogo/dialogo.component';
import { StringDecoder } from 'string_decoder';



@Injectable({
  providedIn: 'root'
})
export class LoginServicioService {

  public name: string;
  private _token: string;
  jwtHelper = new JwtHelperService();
  userTokenData: Subject<any> = new Subject<any>();
  

  constructor(private router: Router, private http: HttpServicioService) {
    this._token = localStorage.getItem('comandaToken');
   /* this.userTokenData.subscribe((value) => {
      this._token = value;
      console.log(this._token + " constructor login");
    }); {  }*/
   
  }

  isLogued() {  
   // console.log(this._token+" islogued") 
    if(this._token == "null" || this._token == null ){
      return null;
    }else
    {
      return this.jwtHelper.isTokenExpired(this._token);
    }     
    }



  public isLogued55() {
    try {
      let rta = this.jwtHelper.isTokenExpired(this._token) || false;
      return rta;
    
    } catch (error) {
      return false;
    }
  }

  public getToken() {
    try {
      return this.jwtHelper.decodeToken(this._token);
    } catch (error) {
      return undefined;
    }
  }

  public getExpirationDate() {

    try {
      return this.jwtHelper.getTokenExpirationDate(this._token);
    } catch (error) {
      return null;
    }
  }

  public logIn(object: Object) {
    this.http.post ('login', object)
      .then(data => {
        if (data.status == 200 ) {
          this.http.updateTokenHeaders(data.comandaToken);
          localStorage.setItem('comandaToken', data.comandaToken);
          this._token = localStorage.getItem('comandaToken');
          this.userTokenData.next(!this._token);
         window.location.href = '/laComanda';// habilitar web
         // window.location.href = '/';
        } else {
          //this.popup.show("error", data.mensaje);+
          console.info("error", data.error);
         // this.alert.mostrar("ERROR",data.error);
         window.alert(String(data.error));
        }
      })
      .catch(e => {
        console.info(e);
      });
  }

  public logOut() {
    try {
      localStorage.setItem('comandaToken', null);
      this.http.updateTokenHeaders('');
      window.location.href = '/laComanda';
    // window.location.href = '/';
    } catch (error) {
      return false;
    }
  }

  public getSector() {
    if (this.jwtHelper.decodeToken(this._token)) //data.tipo) || this.jwtHelper.decodeToken(this._token).data.tipo=== 0)
      return this.jwtHelper.decodeToken(this._token).data.tipo;
    else
    return null;
     // return 1000;
  }

  public getUser() {
    if (this.jwtHelper.decodeToken(this._token) )
      return this.jwtHelper.decodeToken(this._token).data.usuario;
    else
    return null;
     // return 1000;
  }


}
