import { Component, OnInit, Output,EventEmitter } from '@angular/core';

@Component({
  selector: 'app-captcha',
  templateUrl: './captcha.component.html',
  styleUrls: ['./captcha.component.css']
})
export class CaptchaComponent implements OnInit {
  // Usamos el decorador Output
  @Output() PasameLaCaptcha = new EventEmitter();


  ocultarVerificar : boolean;
  ocultarPalabra : boolean;
  palabraDesorganizada: string;
  palabraCorrecta : string;
  palabraIngresada : string;
  ocultarError : boolean;
  ocultarCorrecto : boolean;
  ocultarSpinner : boolean;
  captcha: boolean;

  arrayAnagrama : Array<any> = [
    {
     "palabra": ["QUILmes"],
     "correcta" : "QUILmes"
    },
    {
     "palabra": ["PerrO"],
     "correcta" : "PerrO"
    }, 
    {
      "palabra": ["Cerveza"],
      "correcta" : "Cerveza"
     }, 
     {
      "palabra": ["corrientes"],
      "correcta" : "corrientes"
     }, 
    {
     "palabra": ["QuEso"],
     "correcta" : "QuEso"
    }];
   
   

  constructor() {

    this.captcha= false;
    this.palabraDesorganizada = null;
    this.ocultarVerificar = true;
    this.ocultarPalabra = true;
    this.ocultarError = true;
    this.ocultarCorrecto =true;
    this.ocultarSpinner = true;
   }

  ngOnInit() {
  }

  public generarPalabra()
  {
    let index = Math.floor((Math.random() * 4) + 0);
    let subIndex = Math.floor((Math.random() * 0) + 0);
    console.log(this.arrayAnagrama[index]["palabra"][subIndex]);
    this.palabraDesorganizada = this.arrayAnagrama[index]["palabra"][subIndex];
    this.palabraCorrecta = this.arrayAnagrama[index]["correcta"];
    this.ocultarPalabra = false;
    this.ocultarVerificar = false;
    this.ocultarSpinner = false;
    

  }

 
  resolver(){

    if(this.palabraIngresada == this.palabraCorrecta){
      console.log("bien");
     
      this.ocultarVerificar = true; 
      this.ocultarError = true;
      this.ocultarCorrecto =false;
      this.ocultarSpinner = true;
      this.captcha= true;
      this.PasameLaCaptcha.emit({captcha: this.captcha})


    }
    else{
      console.log("mal"); 
      this.ocultarError = false;
      this.ocultarCorrecto =true;
      this.PasameLaCaptcha.emit({captcha: this.captcha})
    }
  }
  

}
