import { Component, OnInit } from '@angular/core';
import { MenuServicioService } from '../../servicios/menu-servicio.service';
import { Menu } from '../../clases/menu';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css']
})
export class MenuComponent implements OnInit {
  
  public listado : Array<Menu>;

  constructor(public miHttp : MenuServicioService ) { 
    
    this.listado = new Array<Menu>();
    
    this.lista();
   

  }

  ngOnInit() {
  }

  lista(){
   
   return this.miHttp.listar().
    then( datos => {this.listado = datos})
    .catch( error => {console.log(error);})
  
  }
  

}
